
import React, {useState} from "react"
import Movies from "./movie"
import './styles.css'

function SearchMovies() {

  const [query, setQuery] = useState('')
  const [movies, setMovies] = useState([])

async function onSubmit(e){

  e.preventDefault();

  const url = `https://api.themoviedb.org/3/search/movie?api_key=1e719acef0a246ff1e0e560290c036ba&language=en-US&query=${query}&page=1&include_adult=false`;;

  try {
    const promise = await fetch(url)
    const data = await promise.json()
    setMovies(data.results)
  } catch(err){
    console.error(err)
  }
  
}

function handleChange(e){
const {value} = e.target
  setQuery(value)  
}

  return (
    <div>
      <div>
        <form className="form" onSubmit={onSubmit}>
          <label className="label" htmlFor="query">Search Movie</label>
          <input className="input" type="text" name="query" placeholder="Search for a movie" value={query} onChange={handleChange}></input>
          <button className="button" type="submit" disabled={query === "" && true}> Search </button>
        </form>
        <div className="card-list">
          {movies.filter(movie => movie.poster_path).map(movie => <Movies key={movie.id} movie={movie}/>)}
        </div>
      </div>
    </div>
  );
}

export default SearchMovies;
